import React from 'react';

import './Modal.css';

import Label from '../Labels/Label/Label.js';
import Button from '../Buttons/Button/Button.js';

const Modal = (props) => {
    var { className, ...rest } = props;
    if (className == undefined) {
        className = "Modal";
    }
    else {
        className += " Modal";
    }
    if (!props.showModal) {
        return (
            <div></div>
        );
    }
    return (
        <div className={className} {...rest}>
            <div className="ModalWindow">
                <div className="ModalBody">
                    <Label className="ModalTitle">{props.modalTitle}</Label>
                    <hr/>
                    <div className="ModalContent">{props.children}</div>
                </div>
                <div className="ModalButtonArea">
                    <Button className="ModalButton ModalOkButton" onClick={props.okCallback}>{props.okButton}</Button>
                    <Button className="ModalButton ModalCancelButton" onClick={props.cancelCallback}>{props.cancelButton}</Button>
                </div>
            </div>
        </div>
    );
}

export default Modal;