import React, { Component } from 'react';

import './DonatePage.css';

import { Link } from "react-router-dom";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import Button from "../../components/Buttons/index.js";
import Modal from "../../components/Modal/Modal.js";

import VersionData from "../../components/Versions/VersionData.js";

import Icon from '../../components/Icon/Icon.js';

import githubIcon from '../../img/github.svg';


export default class DonatePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showGithubModal: false,
            showDonationModal: false,
            startDownload: false,
            donationButtonClicked: false
        };
        this.setShowGithubModal = (show) => {
            this.setState({showGithubModal: show});
        }
        this.setShowDonationModal = (show) => {
            this.setState({showDonationModal: show});
        }
        this.setStartDownload = () => {
            this.setState({startDownload: true});
            setTimeout(() => {
                document.getElementById("download").click();
                this.setShowGithubModal(true);
            }, 2000);
        }
        this.setDonationButtonClicked = () => {
            this.setState({donationButtonClicked: true});
        }
        this.downloadVersion = new URL(window.location.href).searchParams.get("download");
        if (VersionData.versionList().indexOf(this.downloadVersion) === -1) {
            this.downloadVersion = undefined;
        }
    }

    componentDidMount() {
        if (this.downloadVersion !== undefined) {
            this.visibilityEventListener = document.addEventListener("visibilitychange", () => {
                if (!document.hidden && this.state.donationButtonClicked && !this.state.startDownload) {
                    this.setStartDownload();
                }
            });
        }
    }

    componentWillUnmount() {
        if (this.downloadVersion !== undefined) {
            document.removeEventListener("visibilitychange", this.visibilityEventListener);
        }
    }

    T(en, ko) {
        if (this.props.language === "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    render() {
        if (this.props.language === "en") {
            var donationButtons = (
                <>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://paypal.me/devhotteok" target="_blank">PayPal</Button.LinkButton>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://toss.me/devhotteok" target="_blank">Toss</Button.LinkButton>
                </>
            );
        }
        else {
            var donationButtons = (
                <>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://toss.me/devhotteok" target="_blank">토스(Toss)</Button.LinkButton>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://toon.at/donate/devhotteok" target="_blank">투네이션(Toonation)</Button.LinkButton>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://twip.kr/donate/devhotteok" target="_blank">트윕(Twip)</Button.LinkButton>
                    <Button.LinkButton onClick={this.setDonationButtonClicked} href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
                </>
            );
        }
        if (this.downloadVersion !== undefined) {
            if (VersionData.versionList().indexOf(this.downloadVersion) < 5) {
                var fileName = "TwitchLink-" + this.downloadVersion + ".exe";
            }
            else {
                var fileName = "TwitchLinkSetup-" + this.downloadVersion + ".exe";
            }
            if (this.state.startDownload) {
                this.download = (
                    <div className="downloadInProgress">
                        <Label>{this.T("Thanks for using TwitchLink!", "TwitchLink를 이용해 주셔서 감사합니다.")}</Label>
                        <Label>{this.T("Your download will begin shortly.", "곧 다운로드가 시작됩니다.")}</Label>
                        <Button.LinkButton style={{margin: "10px auto"}} id="download" href={"https://github.com/devhotteok/TwitchLink/releases/download/" + this.downloadVersion + "/" + fileName}>
                            {this.T("If the download doesn't start automatically, click here.", "다운로드가 진행되지 않는다면 이곳을 누르세요.")}
                        </Button.LinkButton>
                        <Link className="LinkButton helpButton" to={"/help?lang=" + this.props.language} target="_blank">
                            {this.T("Having trouble installing?", "설치하는데 문제가 있나요?")}
                        </Link>
                    </div>
                );
                this.downloadButton = undefined;
            }
            else {
                this.download = (
                    <div className="downloadInProgress">
                        <Label>{this.T("Please contribute to the development of TwitchLink before downloading.", "다운로드 전 TwitchLink의 개발에 기여해 주세요.")}</Label>
                    </div>
                );
                this.downloadButton = (
                    <>
                        <p onClick={() => this.setShowDonationModal(true)} style={{margin: "30px auto", width: "fit-content", color: "gray", fontSize: "13pt", cursor: "pointer", textDecoration: "underline"}}>{this.T("Just Download", "그냥 다운로드")}</p>
                    </>
                );
            }
        }
        return (
            <div className="DonatePage">
                <PageFrame>
                    <Content.TitleBar>{this.T("Become a Sponsor", "후원하기")}</Content.TitleBar>
                    {this.download}
                    <Label className="donateInfo">{this.T("If you like the program, become a sponsor of TwitchLink!", "프로그램이 마음에 드신다면 TwitchLink의 개발을 도와주세요!")}</Label>
                    <Button.ButtonArea>
                        {donationButtons}
                        {this.downloadButton}
                    </Button.ButtonArea>
                    {
                        this.state.donationButtonClicked ?
                            <Label className="thankyou">{this.T("Thanks For Your Help!", "후원해주셔서 감사합니다.")}</Label>
                        :
                            <></>
                    }
                    <Modal showModal={this.state.showGithubModal} modalTitle={this.T("Visit our GitHub!", "TwitchLink GitHub를 방문해 보세요!")} okCallback={() => {this.setShowGithubModal(false);document.getElementById("githubButton").click();}} okButton={this.T("OK", "확인")} cancelCallback={() => {this.setShowGithubModal(false);}} cancelButton={this.T("Cancel", "취소")}>
                        {this.T(
                        "Visit our GitHub!",
                        "TwitchLink GitHub를 방문해 보세요!"
                        )}
                        <br/>
                        {this.T(
                        "Help us improve features and fix bugs.",
                        "기능 개선 및 버그 수정에 참여할 수 있습니다."
                        )}
                        <Button.LinkButton id="githubButton" style={{marginTop: "20px"}} className="githubButton" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                        <Icon src={githubIcon} alt="GitHub" style={{marginRight: "10px", filter: "invert(1)"}}/>
                        GitHub
                        </Button.LinkButton>
                    </Modal>
                    <Modal showModal={this.state.showDonationModal} modalTitle={this.T("Just Download", "그냥 다운로드")} okCallback={() => {this.setShowDonationModal(false);}} okButton={this.T("Donate", "후원하기")} cancelCallback={() => {this.setShowDonationModal(false);this.setStartDownload();}} cancelButton={this.T("Just Download", "그냥 다운로드")}>
                        {this.T(
                        "TwitchLink is an open source project.",
                        "TwitchLink는 개발자의 재능기부로 운영되는 프로젝트입니다."
                        )}
                        <br/>
                        <br/>
                        {this.T(
                        "Please consider donating to help us continue developing TwitchLink.",
                        "TwitchLink를 지속적으로 개발할 수 있도록 후원을 검토해보세요."
                        )}
                        <br/>
                        {this.T(
                        "Your support is a great help to our development.",
                        "여러분의 도움은 TwitchLink의 지속적인 개발에 큰 도움이 됩니다."
                        )}
                    </Modal>
                </PageFrame>
            </div>
        );
    }
}